<template>
  <img width="300" alt="Vue logo" src="./assets/1.jpg">
    <h1>{{firstname}} {{lastname}}</h1>
    <button v-on:click="showalert">แสดงชื่อ</button>
    <button @click="showalert2">นามสกุล</button>
    <h2>{{age}}</h2>
    <button @click="plus">เพิ่มตัวเลข</button>
    <button @click="plus2">ลดตัวเลข</button>

    <button @click="plus3">เพิ่มตัวเลข 5</button>
    <button @click="plus4">ลดตัวเลข 5</button><br><br>
    <form action="">
      <label for="">กรุณากรอกชื่อ : </label>
      <input type="text" v-on:input="setname"><br>
      <label for="">กรุณากรอกชื่อนามสกุล : </label>
      <input type="text" v-on:input="setlname"><br>
      <label for="">กรุณากรอกชื่อเล่น : </label>
      <input type="text" v-on:input="setnickname"><br>
      <label for="">กรุณากรอกอายุ : </label>
      <input type="text" v-on:input="setagee"><br>
      <label for="">กรุณากรอกวัน/เดือน/ปีเกิด : </label>
      <input type="text" v-on:input="setbd"><br>
      <label for="">กรุณากรอกสาขา : </label>
      <input type="text" v-on:input="setmj"><br>
      <label for="">กรุณากรอกห้อง : </label>
      <input type="text" v-on:input="setit"><br>
      <label for="">กรุณากรอกคติประจำใจ : </label>
      <input type="text" v-on:input="setkt"><br>
      <label for="">กรุณากรอกเรียนต่อที่ไหน : </label>
      <input type="text" v-on:input="setct"><br>
      <label for="">กรุณากรอกวิชาที่ไม่ชอบ : </label>
      <input type="text" v-on:input="setdl"><br>
    </form>

      <h2>ชื่อ : {{name}}</h2>
      <h2>นามสกุล : {{lname}}</h2>
      <h2>ชื่อเล่น : {{nickname}}</h2>
      <h2>อายุ : {{agee}}</h2>
      <h2>วัน/เดือน/ปีเกิด : {{bd}}</h2>
      <h2>สาขา : {{mj}}</h2>
      <h2>ห้อง : {{it}}</h2>
      <h2>คติประจำใจ : {{kt}}</h2>
      <h2>สถานที่เรียนต่อ : {{ct}}</h2>
      <h2>วิชาที่ไม่ชอบ : {{dl}}</h2>
</template>

<script>


export default {
  name: 'App',
  data(){
    return{
      firstname : "Sayomphu",
      lastname : "koonsin",
      age : 18,
      name : "",
      lname : "",
      nickname : "",
      agee : "",
      bd : "",
      mj : "",
      it : "",
      kt : "",
      ct : "",
      dl : "",
    }
  },
  methods:{
    showalert(){
      alert(this.firstname)
    },
    showalert2(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    plus2(){
      this.age--
    },
    plus3(){
      this.age+=5
    },
    plus4(){
      this.age-=5
    },
    setname(event){
      this.name = event.target.value;
    },
    setlname(event){
      this.lname = event.target.value;
    },
    setnickname(event){
      this.nickname = event.target.value;
    },
    setagee(event){
      this.agee = event.target.value;
    },
    setbd(event){
      this.bd = event.target.value;
    },
    setmj(event){
      this.mj = event.target.value;
    },
    setit(event){
      this.it = event.target.value;
    },
    setkt(event){
      this.kt = event.target.value;
    },
    setct(event){
      this.ct = event.target.value;
    },
    setdl(event){
      this.dl = event.target.value;
    },
  }
}    
</script>

